___CSS_LOADER_EXPORT___.push([module.id, ".login-form[data-v-8009e0e8]{max-width:600px}.login_bg[data-v-8009e0e8]{background:radial-gradient(circle at 50% 50%,#ed1c24,#ffd503)}.btn_color[data-v-8009e0e8]{background:#e63546!important;color:#fff}.label_color[data-v-8009e0e8]{color:#e63546}", "",{"version":3,"sources":["webpack://./resources/js/auth/pages/otp_input.vue"],"names":[],"mappings":"AAsGA,6BACA,eACA,CAEA,2BAEA,6DACA,CAEA,4BAEA,4BAAA,CADA,UAEA,CAEA,8BACA,aACA","sourcesContent":["<template>\r\n  <div>\r\n    <v-app class=\"login_bg\">\r\n      <v-main>\r\n        <v-container fill-height>\r\n          <v-layout align-center justify-center>\r\n            <v-flex class=\"login-form text-xs-center\">\r\n              <v-card>\r\n                <v-card-text>\r\n                  <form @submit.prevent=\"verifyOtp()\">\r\n                    <div\r\n                      class=\"\r\n                        d-flex\r\n                        align-items-center\r\n                        justify-content-center\r\n                        mb-3\r\n                      \"\r\n                    >\r\n                      <img\r\n                        src=\"/all-assets/common/logo/cpb/cpfivestar.png\"\r\n                        alt=\"\"\r\n                        height=\"50px\"\r\n                      />\r\n                      <h3 class=\"text-center label_color ml-4\">\r\n                        Reset Password\r\n                      </h3>\r\n                    </div>\r\n                    <v-alert dense text type=\"success\">\r\n                      An OTP has been sent to this\r\n                      <strong>{{ $route.params.number }}</strong> number\r\n                    </v-alert>\r\n\r\n                    <h5>Enter OTP</h5>\r\n                    <v-otp-input length=\"4\" v-model=\"form.otp\"></v-otp-input>\r\n\r\n                    <v-btn block small color=\"primary\" type=\"submit\"\r\n                      >Submit</v-btn\r\n                    >\r\n                  </form>\r\n                </v-card-text>\r\n              </v-card>\r\n            </v-flex>\r\n          </v-layout>\r\n        </v-container>\r\n      </v-main>\r\n    </v-app>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport Form from \"vform\";\r\nexport default {\r\n  data() {\r\n    return {\r\n      form: new Form({\r\n        otp: \"\",\r\n        number: \"\",\r\n      }),\r\n    };\r\n  },\r\n\r\n  methods: {\r\n    verifyOtp() {\r\n      this.form.post(\"/verify_otp\").then((response) => {\r\n        Swal.fire(\r\n          response.data.data,\r\n          response.data.msg,\r\n          response.data.status\r\n        ).then(() => {\r\n          if (response.data.status == \"success\") {\r\n            console.log(\"in success\", response.data.owner_id);\r\n            // this.$router.go(\"/login/new-password\");\r\n            this.$router.push({\r\n              name: \"new_password\",\r\n              params: { owner_id: response.data.owner_id },\r\n            });\r\n          }\r\n          if (response.data.expire == \"expired\") {\r\n            this.$router.go({\r\n              name: \"Login\",\r\n            });\r\n          }\r\n        });\r\n      });\r\n    },\r\n  },\r\n\r\n  created() {\r\n    console.log(\"ok\");\r\n    if (!this.$route.params.number) {\r\n      this.$router.push({\r\n        name: \"resetPassword\",\r\n      });\r\n    } else {\r\n      this.form.number = this.$route.params.number;\r\n    }\r\n  },\r\n};\r\n</script>\r\n\r\n\r\n<style scoped>\r\n.login-form {\r\n  max-width: 600px;\r\n}\r\n\r\n.login_bg {\r\n  /* background: #990f02; */\r\n  background: radial-gradient(circle at 50% 50%, #ed1c24, #ffd503);\r\n}\r\n\r\n.btn_color {\r\n  color: white;\r\n  background: #e63546 !important;\r\n}\r\n\r\n.label_color {\r\n  color: #e63546;\r\n}\r\n</style>"],"sourceRoot":""}]);
